@import "./src/scss/init";









































































































































































#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: px2html(10px);
}

.outer-tips {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 999999;
  background-color: rgba(0,0,0,0.5);
  font-family:NotoSansCJKsc-Bold;
  font-size:px2html(18px);
  font-weight:bold;
  .outer-tips-png {
    position: absolute;
    top:px2html(10px);
    right:px2html(22px);
    width: px2html(62px);
    height: px2html(47px);
  }
}

.tips{
  position:fixed;
  top: px2html(60px);
  right:px2html(10px);
  color:#fff;
  padding:px2html(8px);
  border-radius:px2html(10px);
  padding-left:px2html(15px);
  padding-right:px2html(15px);
  z-index:99999;
  //width:px2html(375px);
}

.warn-open-outer{
  width:100%;
  height:100%;
  position:absolute;
  left:0px;
  top:0px;
  z-index:9999;
  background-color: rgba(0,0,0,0.3);
  .warn{
    position:fixed;
    background-color: #fff;
    border-radius: px2html(12px);
    right:50%;
    top:50%;
    width:px2html(290px);
    height:px2html(185px);
    z-index:99;
    transform: translateX(50%) translateY(-50%);
    // color:#fff;
    font-size:px2html(12px);
    display:flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    .title{
      font-size:px2html(18px);
    }
    .center{
      // font-weight: 100;
      width:px2html(185px);
       color: #A3A3A3;
      text-align: left;
      font-size:px2html(12px);
    }

    .warn-open{
      padding: px2html(19px);
      font-size:px2html(12px);
      color:#fff;
      padding-top:px2html(7px);
      padding-bottom:px2html(7px);
      border-radius: px2html(30px);
      border:px2html(1px) #3FA8F4 solid;
      background-color: #3FA8F4;
    }

    .close{
      position:absolute;
      right:px2html(10px);
      top:px2html(10px);
      width:px2html(20px);
      height:px2html(20px);
      filter: brightness(0%);
      // filter: blur;
    }

  }
}

.bottom{
  height:px2html(40px);
  //overflow:hidden;
  background:#312736;
  width:px2html(375px);
  position:fixed;
  bottom:0%;
  left:0px;

  font-size:px2html(15px);
  color:#fff;
  .bottom-img{
    width:px2html(60px);
    position:relative;
    position:absolute;
    height:px2html(61px);
    left:px2html(-3px);
    top:px2html(-16px);
    .bottom-hehe{
      position:absolute;
      height:px2html(60px);
      width: px2html(60px);
      left:px2html(9px);
      top:px2html(-6px);
      z-index:999;
    }
  }

  .bottom-center{
    position:absolute;
    right:52%;
    top:50%;
    width:100%;
    transform: translateX(50%) translateY(-50%);
    color: #837D86;
    font-family: PingFangSC;
  }
  .bottom-dl{
    position:absolute;
    right:px2html(40px);
    top:50%;
    background-color: #5A525E;
    padding:px2html(10px);
    color: #fff;
    padding-top:px2html(1px);
    padding-bottom:px2html(1px);
    border-radius:px2html(18px);
    transform: translateX(50%) translateY(-50%);
  }
}
