.cont {
  position: relative;
  top:px2html(80px);
  font-size: px2html(14px);
  color: #998174;
p{
  font-family: NotoSansCJKsc;
  margin-bottom: px2html(24px);
  text-align: left;
}
}

.light{
    z-index: 999;
    content:'';
    opacity: 0.2;
    position:absolute;
    right:px2html(4px);
    top:px2html(12px);
    width:px2html(5px);
    height:px2html(5px);
    background: url('../assets/light.png');
    background-size: 100% 100%;
}

.light-go{
    animation: mylight 2s infinite alternate;
    transition-timing-function: linear;
}
@keyframes mylight
{
    from {opacity:0.2}
    to {opacity:1}
}

a,a:link,a:visited,a:hover,a:active{
  text-decoration: none;
  color:inherit;
}

